import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import MediaQuery from 'react-responsive'

import ResponsiveSidebar from '../ResponsiveSidebar'
import ResponsiveAnchor from '../ResponsiveAnchor'

import { isSidebarHide, isAnchorHide } from '../../store/selectors'

class Container extends Component {
  render() {
    const { children, sidebarHide, anchorHide } = this.props

    return (
      <MediaQuery maxWidth={768}>
        {matches =>
          matches ? (
            <Row>
              <Col>{children}</Col>
            </Row>
          ) : (
            <Row>
              <Col md={3} xl={2}>
                {!sidebarHide && <ResponsiveSidebar />}
              </Col>
              <Col md={9} xl={8}>
                {children}
              </Col>
              <MediaQuery minWidth={1200}>
                <Col xl={2}>{!anchorHide && <ResponsiveAnchor />}</Col>
              </MediaQuery>
            </Row>
          )
        }
      </MediaQuery>
    )
  }
}

Container.propTypes = {
  children: PropTypes.node.isRequired,
}

const mapStateToProps = state => {
  return {
    sidebarHide: isSidebarHide(state),
    anchorHide: isAnchorHide(state),
  }
}

export default connect(mapStateToProps)(Container)
